import React, {FormEvent, SyntheticEvent, useState} from 'react';
import './App.css';
import AddText from "./components/AddText";
import ListStamps from "./components/ListStamps";
import createStamp, {LifeStamp, merge} from "./lib/stamps.utils";
import useLocalList from "./components/hooks/localStorage.hook";
import InputFile from "./components/InputFile";
import DownloadFile from "./components/DownloadFile";
import {StampsContext} from './components/contexts/StampsContext.contexts';

function App() {
    const {addAtBeginning: addStamp, fetch: fetchStamps, clear: clearStamps} = useLocalList('LIFESTAMPS');
    const [textValue, setTextValue] = useState('');
    const [stamps, setStamps] = useState(fetchStamps<LifeStamp>());


    function onTextInput(e: FormEvent<HTMLTextAreaElement>) {
        setTextValue(e.currentTarget.value);
    }

    function onAddStamp(e: SyntheticEvent<HTMLButtonElement>) {
        if (textValue) {
            const stamp = createStamp(textValue, stamps.length + 1);
            setStamps([stamp, ...stamps]);
            addStamp(stamp);
            setTextValue('');
        }
    }

    function onFileRead(data: LifeStamp[]) {
        const mergedLifeStamps = merge(stamps, data);
        clearStamps();
        mergedLifeStamps.forEach((stamp: string) => addStamp(stamp));
        setStamps(mergedLifeStamps);
    }

    return (
        <div className="App">
            <StampsContext.Provider value={{
                stamps,
                onAddStamp,
                onTextInput,
                textValue
            }}>
                <header className="header">
                    <h2 className="brand"><a href="/lifestamps">LifeStamps</a></h2>
                </header>
                <section className="content">
                    <AddText/>
                    <ListStamps/>
                </section>
                <footer className="footer">
                    <div className="controls">
                        <>JSON: (you can download and upload your data)</>
                        <DownloadFile />
                        <InputFile actions={{onFileRead}}/>
                    </div>
                    <div>&nbsp;</div>
                    <h6>lifestamps by anubhav | 0.15.0-81</h6>
                </footer>
            </StampsContext.Provider>
        </div>
    );
}

export default App;
