import React from "react";
import {StampsContext} from "./contexts/StampsContext.contexts";

export default function AddText() {
    return (
        <StampsContext.Consumer>
            {({onAddStamp, onTextInput, textValue}) => {
                return (
                    <div className="add-text-container">
                        <textarea
                            value={textValue}
                            id="add-text"
                            name="add-text"
                            placeholder="enter text here"
                            onInput={(e) => onTextInput(e)}
                        />
                        <button id="add-text-btn" onClick={onAddStamp}>+</button>
                    </div>
                );
            }}
        </StampsContext.Consumer>
    );
}
