import {append, get, remove, unshift} from "../../lib/localStorge.utils";

export default function useLocalList<T>(key: string) {
    function push(value: T) {
        append<T>(key, value);
    }

    function addAtBeginning<T>(value: T) {
        unshift<T>(key, value);
    }

    function fetch<T>(): T[] {
        let data: T[] = [];
        try {
            data = JSON.parse(get(key) as string || '') || [] as T[];
        } catch (e: any) {
            console.error(e?.message);
        } finally {
            return data;
        }
    }

    function clear() {
        remove(key);
    }

    return {push, fetch, clear, addAtBeginning};
}
