import React from 'react';
import downloadFile from "../icons/file_download_white_24dp.svg";
import {StampsContext} from "./contexts/StampsContext.contexts";

export default function DownloadFile() {
    return <StampsContext.Consumer>
        {({stamps}) => <>{
            stamps.length > 0 &&
            <a
                href={`data:text/json;charset=utf-8,${encodeURIComponent(JSON.stringify(stamps, null, 4))}`}
                download={`lifestamps-${new Date().toLocaleDateString()}.json`}
                className="download-control"
            >
                <img src={downloadFile} alt="download your lifestamps as json"/>
            </a>
        }
        </>
        }
    </StampsContext.Consumer>;
}