import React from "react";
import {LifeStamp} from "../lib/stamps.utils";
import {StampsContext} from './contexts/StampsContext.contexts';

export default function ListStamps() {

    return (
        <StampsContext.Consumer>
            {
                ({stamps}) => {
                    return <div className="stamps">
                        {
                            stamps && stamps.length > 0 &&
                            stamps
                                .map((note: LifeStamp, i: number) => ({
                                    note: note.note,
                                    ts: note.ts,
                                    key: `list-item-${i}`
                                }))
                                .map((values: ListItemT) => <ListItem {...values} />)
                        }
                    </div>;
                }
            }
        </StampsContext.Consumer>
    );
}

type ListItemT = {
    note: string;
    ts: string;
    key: string;
}

function ListItem({note, ts, key}: ListItemT) {
    return <div className="stamp" key={key}>
        <div className="ts">
            {ts}
        </div>
        <div className="note">
            {note}
        </div>
    </div>;
}