function featureAvailable() {
    if (typeof localStorage !== 'undefined') {
        try {
            localStorage.setItem('feature_test', 'yes');
            if (localStorage.getItem('feature_test') === 'yes') {
                localStorage.removeItem('feature_test');
                // localStorage is enabled
                return true;
            } else {
                // localStorage is disabled
                return false;
            }
        } catch (e) {
            // localStorage is disabled
            return false;
        }
    } else {
        // localStorage is not available
        return false;
    }
}

function exists(key: string) {
    if (featureAvailable()) {
        return localStorage.getItem(key) !== null;
    }
}

function unshift<T>(key: string, value: T) {
    if (featureAvailable()) {
        if (exists(key)) {
            const _values = localStorage.getItem(key);
            if (_values) {
                let values = JSON.parse(_values as string);
                values.unshift(value);
                localStorage.setItem(key, JSON.stringify(values));
                return;
            }
        }
        const values = [value];
        localStorage.setItem(key, JSON.stringify(values));
    }
}

function append<T>(key: string, value: T) {
    if (featureAvailable()) {
        if (exists(key)) {
            let values = JSON.parse(localStorage.getItem(key) as string);
            values.push(value);
            localStorage.setItem(key, JSON.stringify(values));
        } else {
            const values = [value];
            localStorage.setItem(key, JSON.stringify(values));
        }
    }
}

function set(key: string, value: string) {
    if (featureAvailable()) {
        localStorage.setItem(key, value);
    }
}

function get(key: string) {
    return featureAvailable() ? localStorage.getItem(key) : 'no localStorage';
}

function remove(key: string) {
    if (featureAvailable() && exists(key)) {
        localStorage.removeItem(key);
    }
}

export {exists, featureAvailable, get, set, append, remove, unshift};
