import {v4 as guid} from 'uuid';

export type LifeStamp = {
    note: string,
    ts: string,
    id: string,
    created_at: number;
    updated_at: number;
    serial: number;
    type: string;
}

function formattedTimeStamp() {
    const d = new Date().toJSON();
    const [day, timeString] = d.split('T');
    const time = timeString.split('.')[0];
    return `${day.replace(/-/g, '.')} ${time}`;
}

export default function createStamp(note: string, serial: number): LifeStamp {
    const dts: number = Date.now();
    return {note, ts: formattedTimeStamp(), id: guid(), created_at: dts, updated_at: dts, serial, type: 'LifeStamp'};
}

function merge<T extends LifeStamp>(stampsA: T[], stampsB: T[]) {
    let obj = stampsA.reduce((a: Object, stamp: T) => {
        a = {...a, [`${stamp.id}`]: stamp};
        return a;
    }, {});
    obj = stampsB.reduce((a: Object, stamp: T) => {
        a = {...a, [`${stamp.id}`]: stamp};
        return a;
    }, obj);
    const merged = Object.values(obj);
    return merged;
}

export {merge};
